import React, { useEffect, useState } from "react";
import { Switch, Route, Redirect } from "react-router-dom";

// components
import AdminNavbar from "components/Navbars/AdminNavbar.js";
import Sidebar from "components/Sidebar/Sidebar.js";
import HeaderStats from "components/Headers/HeaderStats.js";
import FooterAdmin from "components/Footers/FooterAdmin.js";

// views
import Dashboard from "views/admin/Dashboard.js";
import Maps from "views/admin/Maps.js";
import Settings from "views/admin/Settings.js";
import Tables from "views/admin/Tables.js";

import APIBASE from "API/APIBASE";

export default function Admin() {
  const [devices, setDevices] = useState([]);

  const toggleDeviceState = (device) => {
    APIBASE.put("/device/command", { id: device.id, cmd: "luz" }).then(
      (response) => {}
    );
  };

  const toggleDeviceSpeed = (device, speed) => {
    APIBASE.put("/device/command", {
      id: device.id,
      cmd: "ventilador",
      fanSpeed: "MIN",
    }).then((response) => {});
  };

  function getDevices() {
    APIBASE.get("/device").then((response) => {
      setDevices(response.data);
    });
  }

  useEffect(() => {
    const timer = setInterval(() => {
      getDevices();
    }, 10000);

    return () => {
      clearInterval(timer);
    };
  });

  useEffect(() => {
    getDevices();
  }, []);

  return (
    <>
      <Sidebar />
      <div className="relative md:ml-64 bg-blueGray-100">
        <AdminNavbar />
        {/* Header */}
        <HeaderStats devices={devices} />
        <div className="px-4 md:px-10 mx-auto w-full -m-24">
          <Switch>
            <Route path="/admin/dashboard" exact component={Dashboard} />
            <Route path="/admin/maps" exact component={Maps} />
            <Route path="/admin/settings" exact component={Settings} />
            <Route path="/admin/tables" exact>
              <Tables
                devices={devices}
                toggleDeviceState={toggleDeviceState}
                toggleDeviceSpeed={toggleDeviceSpeed}
              />
            </Route>
            <Redirect from="/admin" to="/admin/dashboard" />
          </Switch>
          <FooterAdmin />
        </div>
      </div>
    </>
  );
}
